.scroll-up {
  position: fixed;
  bottom: 5%;
  left: 5%;
  background: #181818;
  font-size: 2rem;
  display: none;
  color:white;
  z-index: 999;
  opacity:0.8;
  border-radius:10px;
}

@media only screen and (max-width: 750px) {
  .scroll-up {
    display: block;
  }
}



