:root {
  --blue: #265aaf;
  --green: #0b7c1c;
  --orange: #ff7f00;
  --yellow: #e0e014;
  --main: #ffca08;
  font-size: 17px;
}

body {
  background-color: #302e2e;
  overflow: hidden;
  font-family: "Montserrat", sans-serif;
}

#root {
  overflow: hidden;
  font-weight: 400;
  font-size: 1.1rem;
}

.app {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  min-height: 100vh;
  height: 100%;
}

main {
  width: 100%;
}
nav {
  width: 6.5%;
  min-height: 100vh;
  max-height: 100vh;
}
h1 {
  font-weight: 400;
}

@media only screen and (max-width: 1400px) {
  nav {
    width: 8.5%;
  }
}

@media only screen and (max-width: 1024px) {
  nav {
    width: 12%;
    min-height: initial;
    max-height: initial;
  }

  body {
    overflow-y: auto;
    overflow-x: hidden;
  }
}

@media only screen and (max-width: 750px) {
  .app {
    display: grid;
    min-height: initial;
  }

  body, #root, .viewer {
    overflow: initial!important;
  }

  nav {
    width: 100%;
  }
}

@-moz-document url-prefix() {
  .sidebar > .sidebar-list > .sidebar-menu {
    padding-top: 0;
  }

  .sidebar-list {
    flex-wrap: wrap;
    margin: 0;
    height: 100%;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }

  .sidebar-list > .sidebar-social {
    display: none;
  }

  .sidebar-list > .sidebar-menu > li {
    width: 15%;
  }

  .sidebar-list > .sidebar-menu {
    width: 100%;
  }

  .sidebar {
    justify-content: center;
  }
}



