.home-container {
  height: 100%;
  margin-left: 5%;
  font-size: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-height: 100vh;
}
.home-welcome-text {
  font-weight: 200;
  width: 100%;
  letter-spacing: 1px;
}

.home-welcome-text-small {
  font-weight: 200;
  width: 100%;
  letter-spacing: 1px;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.home-welcome-text-small > div {
  display: inline-block;
}

.indent {
  margin-left: 1%;
}

.home-welcome-text-color {
  color: var(--main);
}


@media only screen and (max-width: 750px) {
  .home-container {
    text-align: center;
    margin-left: 2.5%;
    margin-right: 2.5%;
  }
}

@media only screen and (max-width: 510px) {
  .home-container {
    margin-left: 0;
  }

  .home-welcome-text {
    font-size: 5rem;
    color: var(--main);
  }
  .home-welcome-text-small {
    display: none;
  }

  .home-welcome-text-name {
    display: none;
  }
}



