.single_project-container {
  padding: 1% 5% 5% 3%;
  font-family: "Montserrat", sans-serif;
  color: #8a8a8a;
  height: 100%;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}
.single_project-container > h1,
.single_project-container > h3 {
  width: 100%;
}

/* .carousel-container {
  min-height: 40%;
  height: 1px;
} */

.carousel .slide iframe {
  height: 100%;
}

.code-not-available {
  width: initial;
}

.project-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.project-buttons a:not(:first-child) {
  margin-left: 2%;
}

.project-name,
.about-project,
.project-technologies-used {
  color: #cacaca;
  padding-top: 3rem;
  font-weight: bold;
  margin: 0;
  text-align: start;
}

.about-project {
  margin-bottom: 0.4rem;
}

.link-to-website,
.link-to-github {
  min-width: 12rem !important;
  width: initial !important;
  text-decoration: none;
  color: #ffc73b !important;
  margin: 1% 1% 1% 0 !important;
}

.bttn-slant:focus:before,
.bttn-slant:hover:before {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}

.link-to-website::before,
.link-to-github::before {
  background: #181818 !important;
}

.bttn-slant {
  font-weight: bold !important;
}

.carousel .slide {
  background: transparent !important;
}
.carousel {
  width: 85% !important;
  margin: auto !important;
  height: 100%;
}

@media only screen and (max-width: 750px) {
  .carousel {
    width: 100% !important;
  }
}

@media only screen and (min-width: 1340px) {
  .carousel {
    width: 65% !important;
  }
}

div .carousel .slider-wrapper {
  margin-top: 7.5%;
}

.project-figure {
  padding-bottom: 50%;
  margin: 0;
}

.project-description {
  font-size: 1.3rem;
}

.project-description > p {
  line-height: 2rem;
  margin: 0;
}

.project-image {
  /* background-position: 50% 50%; */
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius:3px;
}

.carousel img {
  width: auto !important;
}

.project-technologies {
  padding: 0;
  list-style-type: none;
}

.technology-block {
  display: -ms-inline-flexbox;
  display: inline-flex;
  text-transform: uppercase;
  margin-right: 0.3rem;
  display: inline-block;
  padding: 8px 5px;
  background-color: #313131;
  color: #999;
  border-width: 1px 1px 2px 1px;
  border-style: solid;
  border-color: #222;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: MontSerrat, Tahoma, Arial, sans-serif;
  font-weight: 700;
  font-size: 0.75em;
  line-height: 1em;
  margin: 3px;
  opacity: 1;
  position: relative;
  overflow: hidden;
}

.carousel .control-next.control-arrow:before {
  border-left: 15px solid #fff !important;
}

.carousel .control-prev.control-arrow:before {
  border-right: 15px solid #fff !important;
}

.carousel .control-arrow:before,
.carousel.carousel-slider .control-arrow:before {
  border-top: 15px solid transparent !important;
  border-bottom: 15px solid transparent !important;
}



