.contact-container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}

.contact-container > h1 {
  width: 100%;
  margin-top: 10%;
  margin-left: 5%;
  font-size: 2.5rem;
  color: var(--main);
  font-weight: 400;
}

.contact-container > form {
  width: 50%;
  height: 100%;
  position: relative;
  margin-left: 5%;
}

.contact-container > form > div > input {
  width: 100%;
  background-color: #37393b;
  outline: none;
  border: none;
  color: #a7abb2;
  padding-left: 5%;
}

.contact-container > form > div > textarea {
  width: 100%;
  outline: none;
  border: none;
  background-color: #37393b;
  color: #a7abb2;
  padding-left: 5%;
  font-family: "Raleway", sans-serif;
}

.contact-container > form > input[type="submit"] {
  width: 30%;
  height: 5%;
  margin-top: 5%;
  margin-left: auto;
  position: absolute;
  right: 0;
  background-color: #181818;
  outline: none;
  color: var(--main);
  border: none;
  -webkit-transition: 0.2s all linear;
  -o-transition: 0.2s all linear;
  transition: 0.2s all linear;
  font-family: "Raleway", sans-serif;
  border-radius: 1px;
  font-weight: bold;
}

.contact-container > form > input[type="submit"]:hover {
  cursor: pointer;
  background-color: var(--main);
  color: #181818;
  -webkit-transition: 0.2s all linear;
  -o-transition: 0.2s all linear;
  transition: 0.2s all linear;
}

.contact-container > form > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 10%;
}

.contact-container > form > div > label {
  width: 100%;
  padding-top: 1%;
  display: inline-block;
  /* color: #0b7c1c; */
}

.contact-email {
  color: #616468;
  font-family: "Amatic SC", cursive;
  font-size: 1.5rem;
  width: 30%;
  margin-left: auto;
  word-wrap: break-word;
}

.contact-email-item {
  width: 80%;
  max-height: 50%;
}

.contact-email-item:last-child {
  height: 50%;
}

@media only screen and (max-width: 750px) {
  .contact-container {
    overflow-y: auto;
  }

  .contact-container > form {
    width: 100%;
    margin-right: 5%;
  }

  .contact-email {
    display: none;
  }

  .contact-container > form > div {
    height: 30%;
  }

  .contact-container > form > div > input {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  .contact-container > form > div > textarea {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }

  .contact-container > form > input[type="submit"] {
    position: relative;
    padding: 0.5rem;
  }

  .contact-container > form > div > label {
    padding-top: 1%;
    padding-bottom: 1%;
  }

  .contact-container {
    height: initial;
  }
}

@-moz-document url-prefix() {
  .contact-container > form > div > input,
  textarea {
    box-shadow: none;
  }
}



