.sidebar {
  width: 100%;
  height: 100%;
  border-right: 1px solid black;
  background-color: #181818;
}

.sidebar-logo {
  text-align: center;
}

.logo-image {
  width: 75% !important;
  -webkit-transform: rotate(-30deg);
      -ms-transform: rotate(-30deg);
          transform: rotate(-30deg);
}

.logo-href {
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.logo-href:hover {
  opacity: 1 !important;
}

.sidebar-logo > a {
  text-decoration: none;
  color: white;
}

.sidebar-list {
  margin-top: 0;
  padding: 0;
  color: white;
  list-style-type: none;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}

.sidebar-list > .sidebar-menu > li,
.sidebar-list > .sidebar-social > li {
  width: 100%;
}

.sidebar-list > .sidebar-menu > li > a,
.sidebar-list > .sidebar-social li > a {
  font-size: 1.5rem;
  text-decoration: none;
  position: relative;
  color: white;
  display: block;
  height: 100%;
  text-align: center;
}

.sidebar-list > .sidebar-menu > li > a > span:before {
  width: 100%;
  margin-left: 0;
  position: absolute;
  display: block;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.sidebar-list > .sidebar-menu > li > a > span,
.sidebar-list > .sidebar-social > li > a > span {
  position: relative;
  font-weight: 100;
  display: block;
  height: 100%;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

@media only screen and (min-width: 750px) {
  .sidebar-list > .sidebar-menu > li > a:hover,
  .sidebar-list > .sidebar-social > li > a:hover,
  .active:after {
    /*color:#ff7f00;*/
    color: var(--main);
    font-weight: bold;
  }


  .sidebar-list > .sidebar-menu > li > a:hover:after,
  .sidebar-list > .sidebar-social > li > a:hover:after {
    opacity: 1;
    -webkit-transition: opacity 0.2s ease-in-out;
    -o-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out;
  }

  .sidebar-list > .sidebar-menu > li > a:hover span,
  .sidebar-list > .sidebar-social > li > a:hover span {
    opacity: 0;
    -webkit-transition: opacity 0.2s ease-in-out;
    -o-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out;
  }
}


.sidebar-list > .sidebar-menu > li > a:after {
  color: var(--main);
  font-weight: bold;
}

.sidebar-list > .sidebar-menu > li > a:after,
.sidebar-list > .sidebar-social > li > a:after {
  content: attr(data-name);
  opacity: 0;
  position: absolute;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  width: 100%;
  font-size: 0.9rem;
  -webkit-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}

.active span {
  opacity: 0;
}

.active:after {
  opacity: 1 !important;
  font-weight: bold;
}

.sidebar-list > .sidebar-social {
  -ms-flex-item-align: end;
      align-self: end;
}

.sidebar-list > .sidebar-menu {
  padding-top: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 50%;
  width: 100%;
}

.sidebar-list > .sidebar-social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-bottom: 0.5rem;
  height: 50%;
  width: 100%;
}

@media only screen and (max-width: 750px) {
  .sidebar > .sidebar-list > .sidebar-menu {
    padding-top: 0;
  }

  .sidebar-list {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 0;
    height: 100%;
    padding-top: 0;
    padding-bottom: 0;
  }

  .sidebar-list > .sidebar-social {
    display: none;
  }

  .sidebar-list > .sidebar-menu > li {
    width: 16%;
  }

  .sidebar-list > .sidebar-menu {
    width: 100%;
  }

  .sidebar {
    -ms-flex-pack: center;
        justify-content: center;
  }
}



