.viewer {
  width: 95%;
  color: white;
  -webkit-transition: 0.1s all linear;
  -o-transition: 0.1s all linear;
  transition: 0.1s all linear;
  max-height: 100vh;
  overflow-y: auto;
}

@media only screen and (max-width: 1024px) {
  .viewer {
    min-height: 100vh;
    width: 100%;
    height: 1px;
    overflow-x: hidden;

  }
}



